import VConsole from 'vconsole';
window.vConsole = new VConsole();
import axios from 'axios';
axios.get('/api/v1/current_location')
.then(res => {
	const { appId, timestamp, nonceStr, signature } = res.data;
	wx.config({
		debug: true, // ,apialertpclogpc
		appId: appId, // 
		timestamp: timestamp, // 
		nonceStr: nonceStr, // 
		signature: signature,// 
		jsApiList: ['getLocation'] // JS
	});
	wx.ready(function(){
		wx.getLocation({
			type: 'wgs84', // wgs84gpsopenLocation'gcj02'
			success: function (res) {
				var latitude = res.latitude; // 90 ~ -90
				var longitude = res.longitude; // 180 ~ -180
				var speed = res.speed; // /
				var accuracy = res.accuracy; // 
				alert(latitude + ' ' + longitude);
			}
		});
	})
})
.catch(res => {
	alert(res);
});